.projects-section {
  padding: 60px 0;
  background: white;
}

.project-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 30px;
}

.project-card {
  background: white;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 5px 15px rgba(0,0,0,0.1);
  transition: all 0.3s ease;
  border: 1px solid #e9ecef;
}

.project-card:hover {
  box-shadow: 0 15px 35px rgba(0,0,0,0.15);
}

.project-header {
  background: linear-gradient(135deg, #232f3e, #ff9900);
  color: white;
  padding: 20px;
  position: relative;
  overflow: hidden;
}

.project-header::before {
  content: '';
  position: absolute;
  top: -50%;
  right: -50%;
  width: 100%;
  height: 100%;
  background: rgba(255,255,255,0.1);
  transform: rotate(45deg);
  transition: all 0.3s ease;
}

.project-card:hover .project-header::before {
  right: -30%;
}

.project-header h3 {
  position: relative;
  z-index: 1;
}

.project-body {
  padding: 20px;
}

.tech-tags {
  margin-top: 15px;
}

.tech-tag {
  background: #ff9900;
  color: white;
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 0.8em;
  margin: 4px 4px 4px 0;
  display: inline-block;
  cursor: pointer;
  transition: all 0.2s ease;
}

.tech-tag:hover {
  background: #232f3e;
  transform: translateY(-2px);
}
