* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.6;
  color: #333;
  overflow-x: hidden;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.section-title {
  font-size: 2.5em;
  text-align: center;
  margin-bottom: 50px;
  color: #232f3e;
}

.gradient-bg {
  background: linear-gradient(135deg, #232f3e, #ff9900);
}

.aws-orange {
  color: #ff9900;
}

.aws-dark {
  color: #232f3e;
}

@media (max-width: 768px) {
  .container {
    padding: 0 15px;
  }
  
  .section-title {
    font-size: 2em;
  }
}
